                          url: "/systemManage/sysSet/roleManage",
                          name: "角色管理"
                        },
                        {
                          url: "/systemManage/sysSet/menuManage",
                          name: "功能菜单管理"
                        },
                        {
                          url: "/systemManage/sysSet/journalManage",
                          name: "日志管理"
                        }
                      ]
                    },
                    {
                      name: "基础信息管理",
                      img: "x_xinxiguanli_icon.png",

                      children: [{
                          url: "/systemManage/mesManage/stationList",
                          name: "测站信息管理"
                        },
                        {
                          url: "/systemManage/mesManage/messageSet",
                          name: "监测项目信息设置"
                        },
                        {
                          url: "/systemManage/mesManage/waterEvaluate",
                          name: "水质类别评价标准"
                        },
                        {
                          url: "/systemManage/mesManage/agreeManage",
                          name: "协议管理"
                        },
                        {
                          url: "/systemManage/mesManage/instrucManage",
                          name: "指令管理"
                        },
                        {
                          url: "/systemManage/mesManage/processManage",
                          name: "流程管理"
                        }
                      ]
                    }
                  ]
                }*/
        // {
        //   path: "",
        //   name: "基础配置",
        //   show: false,
        //   children: [
        //     {
        //       name: "站点管理",
        //       children: [
        //         {
        //           path: "/basicConfig/stationMessage",
        //           name: "站点信息"
        //         }
        //       ]
        //     }
        //   ]
        // }
      ], //所有导航数据
      // levMenudata: [], //二级导航数据
      thrMenudata: [], //三级导航数据
      treeNav: [{ name: "区域" }, { name: "流域" }, { name: "自定义" }], //树结构菜单导航
      screenType: "area",
      defaultId: [3],
      clearTime: "",
      checkStrictly: false
    };
  },
  provide() {
    return {
      globelDelet: this.globelDelet
    };
  },
  created() {
    this.getRoute(this.$route);
    // this.systemSet();

    this.getsessionInfo();
    // this.searchTreeData();
  },
  mounted() {

    this.getwarncount()
  },
  methods: {


    //作者：lfh
    //获取预警报警提醒总数目
    getwarncount() {

      var _seit = this;
      this.tableDatayj = [];
      this.tableDatabj = [];
      this.tableDatatq = [];


      this.$http.get("./static/iot/warn_mgmt/api/wrn-service/wrn.json", {}).then((response) => {
        if (response.data.success === true) {
          var list = response.data.data;
          $(".iconfont.icon-baojing").children("i").text(list.length)

          list.forEach(function(item, index) {

            var grp = item.grp;
            var datestart = item.endTm.parseDate().Format("yyyy-M-d");
            var datenew = new Date().Format("yyyy-M-d");
            var odatestart = new Date(datestart);
            var odatenew = new Date(datenew);
            if (odatestart.getTime() == odatenew.getTime()) {
              item.flag = "今天"
            } else {

              item.flag = odatenew.getDate() - odatestart.getDate() + "天之前";
            }
            if (grp == "alm") {
              _seit.tableDatabj.push(item);
            } else if (grp == "wrn") {

              _seit.tableDatayj.push(item);
            } else {
              _seit.tableDatatq.push(item);
            }

          });

          _seit.nav_a=[];
          _seit.nav_a.push({name: "预警", number: _seit.tableDatayj.length ,svg:"wrn"})
          _seit.nav_a.push({name: "报警", number: _seit.tableDatabj.length, svg:"alm"})
          _seit.nav_a.push({name: "提醒", number: _seit.tableDatatq.length ,svg:"crt" })

       




        }
      });

    },





    //设置用户名，获取菜单等设置信息
    systemSet() {

      this.userName = this.toolObj.getCookie("userName");
      //获取菜单
      this.$http
        .get("/AuthorityManagement/roles/permissions/menus/checked", {
          params: {
            roleId: this.toolObj.getCookie("roleId")
          }
        })
        .then(res => {

          this.menuData = res.data.content.dataList;
        });
    },
    //退出登录
    logoutClick() {
            this.toolObj.removeCookie("token");
            this.toolObj.removeCookie("userName");
            this.toolObj.removeCookie("userAccount");
            this.toolObj.removeCookie("userId");
            this.toolObj.removeCookie("roleId");
            this.$router.push("/");
            return;


      this.$http
        .get("/AuthorityManagement/auth/logout", {
          params: {
            token: this.toolObj.getCookie("token")
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            this.toolObj.removeCookie("token");
            this.toolObj.removeCookie("userName");
            this.toolObj.removeCookie("userAccount");
            this.toolObj.removeCookie("userId");
            this.toolObj.removeCookie("roleId");
            this.$router.push("/");
          }
        });
    },
    //预警弹框
    YjnavClick(key) {
      this.YjnavIndex = key;
    },
    //报警弹框
    BjnavClick(key) {
      this.BjnavIndex = key;
    },
    globelDelet(url, id, callback) {
      this.$confirm("是否将选中的数据删除？", "消息提示", {
          type: "warning",
          center: true,
          cancelButtonClass: "noticonsecondBtn",
          confirmButtonClass: "",
          showCancelButton: true,
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(action => {
          this.$http
            .delete(url, {
              params: {
                ids: id
              }
            })
            .then(res => {
              if (res.data.code == 0) {
                this.$message({
                  message: res.data.message,
                  type: "success",
                  duration: 1500
                });
                if (callback) {
                  callback();
                }
              } else {
                this.$message({
                  message: res.data.message,
                  type: "warning",
                  duration: 1500
                });
              }
            });
        })
        .catch(function() {});
    },
    styleFun(img) {
      return {
        "background-image": "url(./static/images/" + img + ")"
      };
    },
    //获取session数据
    getsessionInfo() {
      if (sessionStorage.getItem("treeNavindex")) {
        this.treeNavindex = JSON.parse(sessionStorage.getItem("treeNavindex"));
      }
      if (this.$route.meta.haveTree == "have") {
        if (sessionStorage.getItem("allStatus")) {
          this.allStatus = sessionStorage.getItem("allStatus");
        }
        if (sessionStorage.getItem("detailTree") == "false") {
          this.detailTree = false;
        } else if (sessionStorage.getItem("detailTree") == "true") {
          this.detailTree = true;
        }
        if (sessionStorage.getItem("opentreeshow") == "false") {
          this.opentreeshow = false;
        } else if (sessionStorage.getItem("opentreeshow") == "true") {
          this.opentreeshow = true;
        }
      } else if (this.$route.meta.haveTree == "haveHidden") {
        if (sessionStorage.getItem("allStatusA")) {
          this.allStatus = sessionStorage.getItem("allStatusA");
        }
        if (sessionStorage.getItem("detailTreeA") == "false") {
          this.detailTree = false;
        } else if (sessionStorage.getItem("detailTreeA") == "true") {
          this.detailTree = true;
        }
        if (sessionStorage.getItem("opentreeshowA") == "false") {
          this.opentreeshow = false;
        } else if (sessionStorage.getItem("opentreeshowA") == "true") {
          this.opentreeshow = true;
        }
      }
      if (sessionStorage.getItem("showCheckbox") == "false") {
        this.showCheckbox = false;
      } else if (sessionStorage.getItem("showCheckbox") == "true") {
        this.showCheckbox = true;
      }
    },
    //树节点筛选
    filterNode(value, data) {
      if (!value) return true;
      return data.nodeName.indexOf(value) !== -1;
    },
    //树结构导航点击
    treeNavclick(item, key) {
      this.treeNavindex = key;
      sessionStorage.setItem("treeNavindex", key);
      if (item.name == "区域") {
        this.filter = "";
        this.screenType = "area";
        this.searchTreeData();
      } else if (item.name == "流域") {
        this.filter = "";
        this.screenType = "basinn";
        this.searchTreeData();
      } else if (item.name == "自定义") {
        this.screenType = "custom";
        this.searchTreeData();
      }
    },
    //站点左切换
    stationLeft() {
      if (this.stationCode == 0) {
        return false;
      }
      this.stationCode--;
      this.staionPos = this.stationCode * 34;
    },
    //站点右切换
    stationRight() {
      if (this.stationCode > this.$store.state.stationInfo.length - 2) {
        return false;
      }
      this.stationCode++;
      this.staionPos = this.stationCode * 34;
    },
    //关闭树
    closeTree() {
      //  this.detailTree = false;
      //  this.allStatus='statusC';
      this.detailTree = false;
      this.$store.commit("setDetailTree", "false");

      if (this.haveTree == "have") {
        this.allStatus = "statusA";
        sessionStorage.setItem("allStatus", "statusA");
        sessionStorage.setItem("detailTree", false);
      } else if (this.haveTree == "haveHidden") {
        this.allStatus = "statusB";
        sessionStorage.setItem("allStatusA", "statusB");
        this.opentreeshow = true;
        sessionStorage.setItem("opentreeshowA", true);
        sessionStorage.setItem("detailTreeA", false);
      } else {
        this.allStatus = "statusB";
        sessionStorage.setItem("allStatusA", "statusB");
        sessionStorage.setItem("detailTree", false);
      }
    },
    //开启树
    openTree() {
      this.detailTree = true;
      this.$store.commit("setDetailTree", "true");

      this.allStatus = "statusC";
      this.opentreeshow = false;
      sessionStorage.setItem("detailTreeA", true);
      sessionStorage.setItem("allStatusA", "statusC");
      sessionStorage.setItem("opentreeshowA", false);
    },
    openMidtree() {
      this.detailTree = true;
      this.$store.commit("setDetailTree", "true");
      this.allStatus = "statusC";
      sessionStorage.setItem("detailTree", true);
      sessionStorage.setItem("allStatus", "statusC");
      this.staionPos = 0;
      this.stationCode = 0;
    },
    //树单选
    treeNodeclick(data) {
      if (data.structureType == "3") {
        this.$store.commit("setStationinfo", data);
        this.$store.commit("setisChange", "false");
      }
    },
    //树多选
    treeCheckchange(node) {
      this.$route.meta.strictly;
      if (this.$route.meta.strictly) {
        if (node.structureType == "3" && this.$route.meta.stationDisable) {
          this.$refs.checkedTree.setCheckedNodes([]);
          var nodes = this.$store.state.strictlyinfo;
          this.$refs.checkedTree.setCheckedNodes(nodes);
          return false;
        }

        this.$refs.checkedTree.setCheckedNodes([]);
        this.$refs.checkedTree.setCheckedNodes([node]);
        var nodes = this.$refs.checkedTree.getCheckedNodes();
        var data = [];
        for (let i in nodes) {
          data.push(nodes[i]);
        }
        this.$store.commit("setStrictlyinfo", data);
        //  this.$store.commit("setisChange", "false");
      } else {
        var nodes = this.$refs.checkedTree.getCheckedNodes();
        var data = [];
        for (let i in nodes) {
          if (nodes[i].structureType == "3") {
            data.push(nodes[i]);
          }
        }
        this.$store.commit("setisChange", "false");
        this.$store.commit("setStationinfo", data);
      }
    },
    //一级导航点击
    menulistClick(item, key) {


      this.$store.commit("setMenuIndex", key);
      this.$store.commit("setNavTitle", item.name);
      this.$store.commit("setLevmenuIndex", 0);
      // if ( item.children.length==0 item.name == "GIS一张图" || item.name == "知识库" || item.name == "预警总览" || item.name == "预警总览" )

      if (item.children.length == 0) {
        this.$store.commit("setLevMenudata", []);
        this.$router.push(item.url);

        return false;
      }

      this.$store.commit("setLevMenudata", item.children);
      if (item.children.length > 0) {
        if (item.children[0].length > 0) {
          this.$store.commit("setChildMenu", item.children[0].children[0].url);
          this.$router.push(item.children[0].children[0].url);
        } else {
          this.$store.commit("setChildMenu", item.children[0].url);
          this.$router.push(item.children[0].url);
        }
      }

    },
    //二级导航点击
    goRouter(item, key) {

      this.$store.commit("setLevmenuIndex", key);
      if (item.children && item.children.length > 0 && item.children[0].children && item.children[0].children.length > 0) {
        this.$router.push(item.children[0].url);
        this.$store.commit("setChildMenu", item.children[0].url);
      } else if (item.children && item.children.length > 0 && item.children[0].children.length == 0) {
        this.$router.push(item.children[0].url);
      } else {
        this.$router.push(item.url);
      }


    },
    //三级导航点击
    goChildrouter(item) {

      this.$store.commit("setChildMenu", item.url);
      this.$store.commit("setLevmenuIndex", this.menuCode);
      this.$router.push(item.url);
      this.menushow = false;
    },
    mouseLeave(event) {
      //谷歌浏览器下点击事件会触发鼠标移出事件bug解决
      if (event.relatedTarget != null) {
        this.menushow = false;
        clearTimeout(this.clearTime);
      }
    },
    //鼠标经过2级菜单时更换3级菜单数据
    levmenuHover(item, key) {

      if (item.children.length > 0) {
        this.clearTime = setTimeout(() => {
          this.menushow = true;
          this.menuCode = key;
          this.thrMenudata = item;
        }, 200);
      }



    },
    menuOut() {
      clearTimeout(this.clearTime);
    },
    //获取当前路由
    getRoute(params) {
      if (this.$route.meta.haveTree == "not") {
        this.haveTree = "not";
        this.allStatus = "statusB";
        this.opentreeshow = false;
      } else if (this.$route.meta.haveTree == "have") {
        this.haveTree = "have";
        this.allStatus = "statusA";
        this.opentreeshow = false;
      } else if (this.$route.meta.haveTree == "haveHidden") {
        this.haveTree = "haveHidden";
        this.allStatus = "statusB";
        this.opentreeshow = true;
      }
      if (params.name == "企业详情") {
        var list = params.matched;
        for (let i in list) {
          if (i > 0 && list[i].name != "企业详情") {
            this.tabNavone.push({
              name: list[i].name,
              path: list[i].path
            });
          }
        }

        this.tabNavone.push({
          name: "运维单位管理",
          path: "/operaAndmaint/baseInfo/operationCompany"
        });
        this.tabNavone.push({ name: params.query.name, path: "" });
      } else if (params.name == "任务管理(副本)") {
        var list = params.matched;
        for (let i in list) {
          if (i > 0 && list[i].name != "任务管理(副本)") {
            this.tabNavone.push({
              name: list[i].name,
              path: list[i].path
            });
          }
        }
      } else if (params.meta.tapCode) {
        var list = params.matched;
        for (let i in list) {
          if (
            i > 0 &&
            list[i].name != "任务管理(副本)" &&
            i != list.length - 1
          ) {
            this.tabNavone.push({
              name: list[i].name,
              path: list[i].path
            });
          }
        }
      } else {
        var list = params.matched;
        for (let i in list) {
          if (i > 0) {
            this.tabNavone.push({
              name: list[i].name,
              path: list[i].path
            });
          }
        }
      }
    },
    //设置树的状态
    setTreestatus() {
      if (this.$route.meta.singleSelect) {
        this.showCheckbox = true;
        this.filter = "";
        sessionStorage.setItem("showCheckbox", true);
        this.defaultId = [];
        this.staionPos = 0;
        this.stationCode = 0;
        this.$nextTick(() => {
          if (this.$refs.tree && this.$store.state.stationInfo.length > 0) {
            //单选树默认选择vuex存储的第一个数据
            this.$refs.tree.setCurrentKey(this.$store.state.stationInfo[0]);
            this.defaultId.push(this.$store.state.stationInfo[0].nodeId);
            this.$store.commit(
              "setStationinfo",
              this.$store.state.stationInfo[0]
            );
            this.$store.commit("setisChange", "false");
          } else {
            console.log(this.treeData)
            if (this.treeData.length == 0) {
              return false;
            }
            //根据树数据状态,默认选中第三级或者第二级数据
            if (this.treeData[0].children[0].children[0]) {
              this.$store.commit(
                "setStationinfo",
                this.treeData[0].children[0].children[0]
              );
              this.$store.commit("setisChange", "false");
            } else {
              this.$store.commit(
                "setStationinfo",
                this.treeData[0].children[0]
              );
              this.$store.commit("setisChange", "false");
            }
          }
        });
      } else {
        this.showCheckbox = false;
        sessionStorage.setItem("showCheckbox", false);
        if (this.$route.meta.strictly) {
          this.checkStrictly = true;
        } else {
          this.checkStrictly = false;
        }
        this.filter = "";
        this.defaultId = [];
        this.staionPos = 0;
        this.stationCode = 0;
        this.$nextTick(() => {
          if (this.$refs.checkedTree) {
            let checkArr = this.treeData;
            let arr = [];
            let station = "";

            if (this.$route.meta.strictly) {
              arr.push(checkArr[0].nodeId);
              this.defaultId = arr;

              this.$refs.checkedTree.setCheckedKeys(arr);
              let checkedNode = this.$refs.checkedTree.getCheckedNodes();
              let setNode = [];
              setNode.push(checkedNode[0]);
              this.$store.commit("setStrictlyinfo", setNode);
              //  this.$store.commit("setisChange", "false");
            } else {
              for (let i in checkArr) {
                arr.push(checkArr[i].nodeId);
              }

              this.defaultId = arr;
              this.$refs.checkedTree.setCheckedKeys(arr);
              let checkedNode = this.$refs.checkedTree.getCheckedNodes();
              let setNode = [];

              for (let i in checkedNode) {
                if (checkedNode[i].structureType == "3") {
                  setNode.push(checkedNode[i]);
                }
              }
              this.$store.commit("setStationinfo", setNode);
              this.$store.commit("setisChange", "false");
            }
          }
        });
      }
    },
    //获取树结构
    searchTreeData() {
      /* 基本信息请求 */
      this.$http
        .get("/SiteManagement/station/queryTree", {
          params: {
            keyName: "",
            typeCode: "",
            isUsedString: "",
            isMonitoring: "",
            basinnOrAreaOrCustom: this.screenType,
            groupName: "",
            groupStations: "",
            roleId: this.toolObj.getCookie("roleId")
          }
        })
        .then(response => {
          let dataJson = response.data;
          if (dataJson.code === 0 && dataJson.content.dataList != null) {
            this.treeData = dataJson.content.dataList;
            this.setTreestatus();
          } else {
            this.treeData = [];
          }
        })
        .catch(error => {});
    }
  },
  computed: {
    stationNames: {
      get: function() {
        let str = "";
        for (let i in this.$store.state.stationInfo) {
          str += this.$store.state.stationInfo[i].nodeName + ",";
        }
        str = str.substring(0, str.length - 1);
        return str;
      }
    },
    stationName: {
      get: function() {
        if (this.$store.state.stationInfo.length > 0) {
          return this.$store.state.stationInfo[0].nodeName;
        } else {
          return "";
        }
      }
    },
    stationNum: {
      get: function() {
        return this.$store.state.stationInfo.length;
      }
    },
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    }
  },
  watch: {
    $route(to, from) {
      this.tabNavone = [];
      this.getRoute(to);

      //路由切换时控制树结构的按钮显示隐藏
      this.detailTree = false;
      if (this.$route.meta.haveTree == "not") {
        this.haveTree = "not";
        this.allStatus = "statusB";
        this.opentreeshow = false;
      } else if (this.$route.meta.haveTree == "have") {
        this.haveTree = "have";
        this.allStatus = "statusA";
        this.opentreeshow = false;
        if (sessionStorage.getItem("allStatus")) {
          this.allStatus = sessionStorage.getItem("allStatus");
        }
        if (sessionStorage.getItem("detailTree") == "false") {
          this.detailTree = false;
        } else if (sessionStorage.getItem("detailTree") == "true") {
          this.detailTree = true;
        }
        if (sessionStorage.getItem("opentreeshow") == "false") {
          this.opentreeshow = false;
        } else if (sessionStorage.getItem("opentreeshow") == "true") {
          this.opentreeshow = true;
        }
      } else if (this.$route.meta.haveTree == "haveHidden") {
        this.haveTree = "haveHidden";
        this.allStatus = "statusB";
        this.opentreeshow = true;
        if (sessionStorage.getItem("allStatusA")) {
          this.allStatus = sessionStorage.getItem("allStatusA");
        }
        if (sessionStorage.getItem("detailTreeA") == "false") {
          this.detailTree = false;
        } else if (sessionStorage.getItem("detailTreeA") == "true") {
          this.detailTree = true;
        }
        if (sessionStorage.getItem("opentreeshowA") == "false") {
          this.opentreeshow = false;
        } else if (sessionStorage.getItem("opentreeshowA") == "true") {
          this.opentreeshow = true;
        }
      }
      this.setTreestatus();
    },
    filter(val) {
      this.$refs.tree.filter(val);
      this.$refs.checkedTree.filter(val);
    },
    stationInfo: function(newValue, oldValue) {
      if (this.$store.state.isChange == "true") {
        this.filter = newValue[0].nodeName;
      }
    }
  }
};

</script>
<style scoped lang="less">
.taskpage {
  height: 100%;
  background: #eff3f5;

  .task_nav {
    float: left;
    height: 100%;
    width: 198px;
    border: 1px solid #d9e6f7;
    background: #fff;

    .tp {
      height: 46px;
      line-height: 46px;
      padding-left: 40px;
      color: #1a1a1a;
      position: relative;

      i {
        color: #166bce;
        position: absolute;
        left: 18px;
        top: 15px;
        font-size: 16px;
        line-height: 16px;
      }
    }

    ul {
      li {
        color: #666;
        height: 35px;
        line-height: 35px;
        cursor: pointer;
        position: relative;

        .div {
          span {
            position: absolute;
            width: 9px;
            height: 9px;
            border-radius: 50%;
            border: 1px solid #666;
            left: 40px;
