          cursor: pointer;

          &:hover {
            background: url("../../assets/images/hover_shuClose.png") center center no-repeat;
          }

          i {
            width: 3px;
            height: 8px;
            position: absolute;
            left: 2px;
            top: 50%;
            transform: translateY(-4px);
            background: url("../../assets/images/righticon.png") center center no-repeat;
          }
        }
      }

      .mainbox {
        height: calc(100% - 12px);

        width: calc(100% - 222px);
        position: absolute;
        background: #fff;
        left: 216px;
        top: 6px;
        transition: all 0.3s;
      }
    }

    .fillFull {
      width: 100%;
      left: 0px;
    }

    .fullFull {
      width: calc(100% - 12px) !important;
      left: 6px !important;
    }

    .midFull {
      width: calc(100% - 83px) !important;
      left: 78px !important;
    }
  }
}

</style>



